.popup__container {
    background-color: white;
    min-width: 430px;
    position: relative;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    z-index: 1;
}

@media screen and (max-width: 500px) {
    .popup__container {
        min-width: 288px;
    }
    
}

